<template>
    <div>
        <v-select :data-key="select"></v-select>
    </div>
</template>
<script>
export default {
    data() {
        return {
            select: {
                val: "1",
                sortArray: [{
                    title: "Option 1",
                    value: "1"
                }, {
                    title: "Option 2",
                    value: "2"
                }, {
                    title: "Option 3",
                    value: "3"
                }],
                maxlength: 3,
                hasManual: true,
                manualText: "手动配置",
                valid: [{
                    type: "num",
                    args: [1, 100]
                }],
                changeCallBack: this.changeSelect,
                
            }
        };
    },
    methods: {
        changeSelect(value) {
            console.log("select value is", value);
        }
    }
}
</script>