<template>
    <v-table ref="table" :tableOptions="tableData" @on-custom-comp="customCompFunc"></v-table>
</template>

<script>
export default {
    data() {
        return {
            tableData: {
                key: "index",
                css: "table-group",
                columns: [
                    {
                        title: "序号",
                        field: "index"
                    },
                    {
                        title: "姓名",
                        field: "name"
                    },
                    {
                        title: "性别",
                        field: "sex",
                        format(data) {
                            return data == 1 ? "女" : "男";
                        }
                    },
                    {
                        title: "地址",
                        field: "address"
                    },
                    {
                        title: "操作",
                        action: ["edit", "delete"],
                        componentName: 'table-operation'
                    }
                ],
                originData: [
                    {
                        index: 1,
                        name: "张三",
                        sex: "0",
                        address: "深圳市南山区xxx号"
                    },
                    {
                        index: 2,
                        name: "李四",
                        sex: "1",
                        address: "深圳市南山区xxx号"
                    },
                    {
                        index: 3,
                        name: "王五",
                        sex: "1",
                        address: "深圳市南山区xxx号"
                    },
                    {
                        index: 4,
                        name: "朱六",
                        sex: "0",
                        address: "深圳市南山区xxx号"
                    },
                    {
                        index: 5,
                        name: "卓七",
                        sex: "1",
                        address: "深圳市南山区xxx号"
                    }
                ]
            }
        };
    },
    mounted() {
        //this.tableData.originData = ;
    },
    methods: {
        customCompFunc(options) {
            switch (options.type) {
                //选中单个
                case "edit":
                    this.editRule(options);
                    break;
                //全选
                case "delete":
                    this.delRule(options);
                    break;
            }
        },
        editRule(options) {
            this.$message("点击编辑");
        },
        delRule(options) {
            this.$message("点击删除");
        }
    }
};
</script>