<template>
    <div>
        <h3>输入框</h3>
        <v-code :code="input1" :isShow="false">
            <template v-slot:descript>
                <v-group>
                    <span class="code">data-key</span>配置输入框的属性
                    <div>
                        <span class="code">val</span>值 <br/>
                        <span class="code">placeholder</span>占位符<br/>
                        <span class="code">changeCallBack</span>修改输入框后执行的回调，参数为输入框的值<br/>
                        <span class="code">type</span>输入框的类型，默认为text<br/>
                        <span class="code">maxlength</span>输入框的最大输入长度<br/>
                        <span class="code">valid</span>输入框的数据验证
                    </div>
                </v-group>
                
            </template>
            <input-list></input-list>
        </v-code>
        <h3>密码框&自定义事件</h3>
        <v-code :code="eventInput" :isShow="false">
            <template v-slot:descript>
                <v-group>
                    <span class="code">data-key</span>配置输入框的属性
                    <div>
                        <span class="code">hasEye</span>是否显示小眼睛，点击切换输入框类型，默认：false <br/>
                        <span class="code">events</span>自定义事件，属性为事件名称，值为执行函数
                    </div>
                </v-group>
                
            </template>
            <input-event></input-event>
        </v-code>
    </div>
</template>
<script>
import inputList from "../example/input/input";
import EventInput from "../example/input/password";
export default {
    components: {
        "input-list": inputList,
        "input-event": EventInput
    },
    data() {
        return  {
            input1: this.$store.state.input.input1,
            eventInput: this.$store.state.input.eventInput,
        };
    },
    methods: {

    }
}
</script>