let confirm= `
<template>
    <div>
        <span class="btn" @click="confirm">确认框</span>
        <div v-show="show">显示信息</div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            show: false
        };
    },
    methods: {
        confirm() {
            this.$confirm("是否显示信息")
                .then(() => {
                    this.show = true;
                })
                .catch(e => {
                    this.show = false;
                });
        }
    }
};
</script>
`;

let alert1 = `
<template>
    <div>
        <span class="btn" @click="alert">警告框</span>
        <div v-show="show">显示信息</div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            show: false
        };
    },
    methods: {
        alert() {
            this.$alert({
                title: "警告",
                parseHtml: true,
                okText: "确定",
                content: "<div class='red'>这是一个警告框</div>"
            })
                .then(() => {
                    this.show = !this.show;
                })
        }
    }
};
</script>
`;

var state = {
    confirm: confirm,
    alert: alert1
};
var getters = {};

export default {
    namespaced: true,
    state,
    getters
}