<template>
    <div>
        <span class="btn" @click="confirm">确认框</span>
        <div v-show="show">显示信息</div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            show: false
        };
    },
    methods: {
        confirm() {
            this.$confirm("是否显示信息")
                .then(() => {
                    this.show = true;
                })
                .catch(e => {
                    this.show = false;
                });
        }
    }
};
</script>