let customTable = `
<template>
    <v-table
        ref="table"
        :tableOptions="tableData"
        @on-custom-comp="customCompFunc"
    ></v-table>
</template>

<script>
export default {
    data() {
        return {
            tableData: {
                key: "index",
                css: "table-group",
                selectBox: true,
                columns: [
                    {
                        title: "序号",
                        field: "index"
                    },
                    {
                        title: "姓名",
                        field: "name"
                    },
                    {
                        title: "性别",
                        field: "sex",
                        format(data) {
                            return data == 1 ? "女" : "男";
                        }
                    },
                    {
                        title: "地址",
                        field: "address"
                    }
                ],
                originData: [
                    {
                        index: 1,
                        name: "张三",
                        sex: "0",
                        address: "深圳市南山区xxx号"
                    },
                    {
                        index: 2,
                        name: "李四",
                        sex: "1",
                        address: "深圳市南山区xxx号"
                    },
                    {
                        index: 3,
                        name: "王五",
                        sex: "1",
                        address: "深圳市南山区xxx号"
                    },
                    {
                        index: 4,
                        name: "朱六",
                        sex: "0",
                        address: "深圳市南山区xxx号"
                    },
                    {
                        index: 5,
                        name: "卓七",
                        sex: "1",
                        address: "深圳市南山区xxx号"
                    }
                ]
            }
        };
    },
    methods: {
        customCompFunc(options) {
            switch (options.type) {
                //选中单个
                case "checkbox":
                    this.clickSelectBox(options);
                    break;
                //全选
                case "selectAll":
                    this.clickSelectedAll(options);
                    break;
            }
        },
        clickSelectBox(options) {
            this.$message("点击单个复选框");
        },
        clickSelectedAll(options) {
            this.$message("点击全选复选框");
        }
    }
};
</script>
`;

