let customCom = `
//全局组件
<template>
    <div>
        <a
            v-if="hasEdit"
            class="table-action"
            v-tooltip="editText"
            @click.stop.prevent="handler('edit', field)"
            
        >{{editText}}</a>
        <a
            v-if="hasDel"
            class="table-action icon-delete"
            v-tooltip="cancelText"
            @click.stop.prevent="handler('delete', field)"
        >{{cancelText}}</a>
        <a
            v-if="hasView"
            class="table-action icon-view"
            v-tooltip="viewText"
            @click.stop.prevent="handler('view', field)"
        ></a>
        <a v-if="!hasEdit && !hasDel && !hasView">--</a>
    </div>
</template>
<script>
export default {
    name: "table-operation",
    props: ["rowData", "field", "index", "tableData", "originData", "action", "keyword"],
    data() {
        return {
            editText: "编辑",
            cancelText: "删除",
            viewText: "查看"
        };
    },
    computed: {
        hasEdit() {
            return this.action.indexOf("edit") != -1 && this.rowData.hasEdit !== false;
        },
        hasDel() {
            return this.action.indexOf("delete") != -1 && this.rowData.hasDel !== false;
        },
        hasView() {
            return this.action.indexOf("view") != -1 && this.rowData.hasView !== false;
        }
    },
    methods: {
        handler(type) {
            // 参数根据业务场景随意构造
            let params = {
                type: type,
                index: this.index,
                rowData: this.originData
            };
            this.$emit("on-custom-comp", params);
        }
    }
};
</script>

//源代码
<template>
    <v-table ref="table" :tableOptions="tableData" @on-custom-comp="customCompFunc"></v-table>
</template>

<script>
export default {
    data() {
        return {
            tableData: {
                key: "index",
                css: "table-group",
                columns: [
                    {
                        title: "序号",
                        field: "index"
                    },
                    {
                        title: "姓名",
                        field: "name"
                    },
                    {
                        title: "性别",
                        field: "sex",
                        format(data) {
                            return data == 1 ? "女" : "男";
                        }
                    },
                    {
                        title: "地址",
                        field: "address"
                    },
                    {
                        title: "action",
                        action: ["edit", "delete"],
                        componentName: 'table-operation'
                    }
                ],
                originData: [
                    {
                        index: 1,
                        name: "张三",
                        sex: "0",
                        address: "深圳市南山区xxx号"
                    },
                    {
                        index: 2,
                        name: "李四",
                        sex: "1",
                        address: "深圳市南山区xxx号"
                    },
                    {
                        index: 3,
                        name: "王五",
                        sex: "1",
                        address: "深圳市南山区xxx号"
                    },
                    {
                        index: 4,
                        name: "朱六",
                        sex: "0",
                        address: "深圳市南山区xxx号"
                    },
                    {
                        index: 5,
                        name: "卓七",
                        sex: "1",
                        address: "深圳市南山区xxx号"
                    }
                ]
            }
        };
    },
    mounted() {
        //this.tableData.originData = ;
    },
    methods: {
        customCompFunc(options) {
            switch (options.type) {
                //选中单个
                case "edit":
                    this.editRule(options);
                    break;
                //全选
                case "delete":
                    this.delRule(options);
                    break;
            }
        },
        editRule(options) {
            this.$message("点击编辑");
        },
        delRule(options) {
            this.$message("点击删除");
        }
    }
};
</script>
`;

var state = {
    table1: table1,
    customTable: customTable,
    pageTable: pageTable,
    customComTable: customCom
};
var getters = {};

export default {
    namespaced: true,
    state,
    getters
}