let select1 = `
<template>
    <div>
        <v-select :data-key="select"></v-select>
    </div>
</template>
<script>
export default {
    data() {
        return {
            select: {
                sortArray: [{
                    title: "Option 1",
                    value: "1"
                }, {
                    title: "Option 2",
                    value: "2"
                }, {
                    title: "Option 3",
                    value: "3"
                }],
                changeCallBack: this.changeSelect
            }
        };
    },
    methods: {
        changeSelect(value) {
            console.log("select value is", value);
        }
    }
}
</script>
`;

let customSelect = `
<template>
    <div>
        <v-select :data-key="select"></v-select>
    </div>
</template>
<script>
export default {
    data() {
        return {
            select: {
                val: "1",
                sortArray: [{
                    title: "Option 1",
                    value: "1"
                }, {
                    title: "Option 2",
                    value: "2"
                }, {
                    title: "Option 3",
                    value: "3"
                }],
                hasManual: true,
                maxlength: 3,
                manualText: "手动配置",
                valid: [{
                    type: "num",
                    args: [1, 100]
                }],
                changeCallBack: this.changeSelect,
                
            }
        };
    },
    methods: {
        changeSelect(value) {
            console.log("select value is", value);
        }
    }
}
</script>
`;

var state = {
    select1: select1,
    customSelect: customSelect
};
var getters = {};

export default {
    namespaced: true,
    state,
    getters
  }