<template>
    <div>
        <h3>开关</h3>
        <v-code :code="switch1" :isShow="false">
            <template v-slot:descript>
                <v-group>
                    <span class="code">values</span>开关值，第一项是开关开启值，第二项是开关关闭值
                    <br />
                    <span class="code">changeCallBack</span>开关切换后的回调函数，参数为开关当前值
                    <br />
                    <span class="code">beforeChange</span>开关切换前执行的函数，返回false时不会执行changeCallBack
                    <br />
                </v-group>
            </template>
            <switch-list></switch-list>
        </v-code>
        <table class="table-default">
            <thead>
                <tr>
                    <th>
                        <span>参数</span>
                    </th>
                    <th>
                        <span>类型</span>
                    </th>
                    <th>
                        <span>默认值</span>
                    </th>
                    <th>
                        <span>意义</span>
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>
                        <span>css</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>样式</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>show</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>true</span>
                    </td>
                    <td>
                        <span>是否显示</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>disabled</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>false</span>
                    </td>
                    <td>
                        <span>是否禁用</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>val</span>
                    </td>
                    <td>
                        <span>String or Boolean</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>值</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>immediate</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>true</span>
                    </td>
                    <td>
                        <span>是否立即执行回调函数</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>name</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>组件名称</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>values</span>
                    </td>
                    <td>
                        <span>Array</span>
                    </td>
                    <td>
                        <span>[true, false]</span>
                    </td>
                    <td>
                        <span>开启和关闭的值</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>changeCallBack</span>
                    </td>
                    <td>
                        <span>Function</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>切换开关后执行的回调函数，参数开关当前值</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>beforeChange</span>
                    </td>
                    <td>
                        <span>Function</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>切换之前执行的函数，如果返回false，则不执行changeCallBack</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>events</span>
                    </td>
                    <td>
                        <span>Object</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>key为事件类型，value为事件执行函数</span>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
import SwitchList from "../example/switch/switch";

export default {
    components: {
        "switch-list": SwitchList
    },
    data() {
        return {
            switch1: this.$store.state.switch1.switch1
        };
    },
    methods: {}
};
</script>