let radio = `
<template>
    <div>
        <v-radio :data-key="radio"></v-radio>
        <div>值为：{{radio.val}}</div>
    </div>
</template>
<script>
export default {
    data() {
        return  {
            radio: {
                val: "1",
                sortArray: [{
                    value: "1",
                    title: "开启"
                },
                {
                    value: "0",
                    title: "关闭"
                }],
                changeCallBack(value) {
                    console.log("radio value ",value);
                },
                immediate: false,
                events: {
                    click: this.clickRadio
                }
            }
        };
    },
    methods: {
        clickRadio() {
            console.log("点击单选框，值为", this.radio.val);
        }
    }
}
</script>
`;

let disabled = `
<template>
    <div>
        <v-radio :data-key="radio"></v-radio>
        <div>值为：{{radio.val}}</div>
    </div>
</template>
<script>
export default {
    data() {
        return  {
            radio: {
                val: "1",
                sortArray: [{
                    value: "1",
                    title: "开启"
                },
                {
                    value: "0",
                    title: "关闭"
                },
                {
                    value: "-1",
                    title: "不改变",
                    disabled: true
                }],
                changeCallBack(value) {
                    console.log("radio value ",value);
                },
                immediate: false,
                events: {
                    click: this.clickRadio
                }
            }
        };
    },
    methods: {
        clickRadio() {
            console.log("点击单选框，值为", this.radio.val);
        }
    }
}
</script>
`;
var state = {
    radio: radio,
    disabled: disabled
};
var getters = {};

export default {
    namespaced: true,
    state,
    getters
}