/* 数据验证函数集合对象 */
var utils = {
    getUtf8Length: function (str) {
        var totalLength = 0,
            charCode,
            len = str.length,
            i;

        for (i = 0; i < len; i++) {
            charCode = str.charCodeAt(i);
            if (charCode < 0x007f) {
                totalLength++;
            } else if ((0x0080 <= charCode) && (charCode <= 0x07ff)) {
                totalLength += 2;
            } else if ((0x0800 <= charCode) && (charCode <= 0xffff)) {
                totalLength += 3;
            } else {
                totalLength += 4;
            }
        }
        return totalLength;
    }
};

var valid = {
    len: function (str, min, max) {
        var len = str.length;

        if (len < min || len > max) {
            return _('请输入 %s - %s 个字符', [min, max]);
        }
    },

    byteLen: function (str, min, max) {
        var totalLength = utils.getUtf8Length(str);

        if (typeof min !== "undefined" && typeof max !== "undefined" && (totalLength < min || totalLength > max)) {
            return _("请输入 %s - %s 个字节", [min, max]);
        }
    },

    num: function (str, min, max) {

        if (!(/^([-0-9])?([0-9]+)$/).test(str)) {
            return _("输入值必须为整数");
        }
        if (typeof min === "number" && typeof max === "number") {
            if (parseInt(str, 10) < min || parseInt(str, 10) > max) {
                return _("有效输入范围：%s - %s", [min, max]);
            }
        }
    },

    float: function (str, min, max) {
        //支持小数点后一位
        if (!(/^([-0-9])?([0-9]+)(\.[0-9])?$/).test(str)) {
            return _("输入值格式错误");
        }
        if (typeof min === "number" && typeof max === "number") {
            if (Number(str) < min || Number(str) > max) {
                return _("有效输入范围：%s - %s", [min, max]);
            }
        }
    },

    even: function (str, min, max) {
        let isError = valid.num(str, min, max);
        if (isError) {
            return isError;
        }
        if (str % 2 !== 0) {
            return _("输入值必须为偶数");
        }
    },

    mac: {
        all: function (str) {
            var ret = this.specific(str);

            if (ret) {
                return ret;
            }

            if (!(/^([0-9a-fA-F]{2}:){5}[0-9a-fA-F]{2}$/).test(str) && !(/^([0-9a-fA-F]{2}-){5}[0-9a-fA-F]{2}$/).test(str)) {
                return _("请输入一个有效的MAC地址");
            }
        },

        specific: function (str) {
            var subMac1 = str.split(':')[0];

            if (subMac1.charAt(1) && parseInt(subMac1.charAt(1), 16) % 2 !== 0) {
                return _('第二个字符必须为偶数');
            }
            if (str === "00:00:00:00:00:00") {
                return _('MAC地址不能为00:00:00:00:00:00');
            }
        }
    },
    specialMac: function (str) {
        if (!(/^([0-9a-fA-F]{4}-){2}[0-9a-fA-F]{4}$/).test(str)) {
            return _("请输入一个有效的MAC地址");
        }
    },

    ip: {
        all: function (str) {
            var ret = this.specific(str);

            if (ret) {
                return ret;
            }

            if (!(/^([1-9]|[1-9]\d|1\d\d|2[0-1]\d|22[0-3])\.(([0-9]|[1-9]\d|1\d\d|2[0-4]\d|25[0-5])\.){2}([0-9]|[1-9]\d|1\d\d|2[0-4]\d|25[0-5])$/).test(str)) {
                return _("请输入一个有效的IP地址");
            }
        },

        specific: function (str) {
            var ipArr = str.split('.'),
                ipHead = ipArr[0];

            if (ipArr[0] === '127') {
                return _("以127开头的IP地址为环回地址，请重新输入");
            }
            if (ipArr[0] > 223) {
                return _("%s 为无效值，请输入一个1-223之间的值", [ipHead]);
            }
        }
    },

    dns: function () {},

    mask: {
        all: function (str) {
            var rel = /^(254|252|248|240|224|192|128)\.0\.0\.0$|^(255\.(254|252|248|240|224|192|128|0)\.0\.0)$|^(255\.255\.(254|252|248|240|224|192|128|0)\.0)$|^(255\.255\.255\.(255|254|252|248|240|224|192|128|0))$/;
            if (!rel.test(str)) {
                return _("请输入一个有效的子网掩码");
            }
        }
    },

    email: function (str) {
        var rel = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
        if (!rel.test(str)) {
            return _("请输入一个有效的电子邮箱");
        }

    },

    time: function (str) {
        if (!(/^\d{4}-\d{2}-\d{2} \d{2}:\d{2}:\d{2}$/).test(str)) {
            return _("请输入一个有效的时间");
        }
    },

    hex: function (str) {
        if (!(/^[0-9a-fA-F]{1,}$/).test(str)) {
            return _("必须为十六进制数");
        }
    },

    ascii: function (str, min, max) {
        if (!(/^[ -~]+$/g).test(str)) {
            return _("必须为 ASCII码");
        }
        if (min || max) {
            return valid.len(str, min, max);
        }
    },

    pwd: {
        all: function (str, minLen, maxLen) {
            var ret = this.specific(str);
            if (ret) {
                return ret;
            }

            if (minLen && maxLen) {
                ret = valid.len(str, minLen, maxLen);
                if (ret) {
                    return ret;
                }
            }
        },
        specific: function (str) {
            if (!(/^[0-9a-zA-Z]+$/).test(str)) {
                return _("只能包含数字和字母");
            }
        }
    },

    remark: function (str, min, max) {
        let totalLength = str.length,
            ret;
        if (min && max) {
            ret = $.valid.byteLen(str, min, max);
            if (ret) {
                return ret;
            }
        }

        if (!(/^[0-9a-zA-Z_]+$/).test(str)) {
            return _("请输入数字、字母和下划线");
        }
    },

    username: function (str) {
        if (!(/^\w{1,}$/).test(str)) {
            return _("请输入一个有效的用户名");
        }
    },

    domain: function (str) {
        if (!/^[\d\.]+$/.test(str)) {
            if (/^[0-9a-z]([0-9a-z-]+\.){1,}([0-9a-z])+$/i.test(str) || str == "localhost") {

            } else {
                return _("请输入一个有效的域名");
            }
        } else {

            return _("请输入一个有效的域名");
        }

    },

    ping: function (str) {
        var ip = valid.ip.all(str),
            domain = valid.domain(str);
        if (ip && domain) {
            return _("请输入一个有效的IP地址或域名");
        }
    },

    vlan: function (str, min, max) {
        let msg = _("请输入一个有效的VLAN"),
            vlanArr = str.split(","),
            checkSuccess = true;

        if (!(/^[0-9,-]+$/).test(str)) {
            return msg;
        }

        vlanArr.some((group, index) => {
            let valArr = group.split("-");
            if (valArr.length > 2 || valArr.length < 1) {
                checkSuccess = false;
                return true;
            }
            valArr.some((item) => {
                if (!(/^([-0-9])?([0-9]+)$/).test(item)) {
                    checkSuccess = false;
                    return true;
                }

                if (parseInt(item, 10) < min || parseInt(item, 10) > max) {
                    checkSuccess = false;
                    return true;
                }
            });

            if (valArr.length === 2) {
                if (parseInt(valArr[0], 10) > parseInt(valArr[1], 10)) {
                    checkSuccess = false;
                    return true;
                }
            }
            let hasOverlap = vlanArr.filter((item1, index1) => {
                if (valArr.length === 1) {
                    valArr.push(valArr[0]);
                }
                let newArr = item1.split("-");
                if (newArr.length === 1) {
                    newArr.push(newArr[0]);
                }

                //不在范围或者是自身
                if (Number(valArr[0]) > Number(newArr[1]) || Number(valArr[1]) < Number(newArr[0]) || index1 === index) {

                } else {
                    return newArr;
                }

            });
            if (hasOverlap.length > 0) {
                checkSuccess = false;
                return true;
            }
        });
        if (!checkSuccess) {
            return msg;
        }
    },
    packettypehex: function (str, min, max) {
        if (!(/^[0-9a-fA-F]{1,}$/).test(str)) {
            return _("必须为十六进制数");
        }
        str = '0x' + str;

        if (parseInt(str, 16) < min || parseInt(str, 16) > max) {
            return _("有效输入范围：%s - %s", [min.toString(16), max.toString(16)]);
        }
    }
};

export {
    valid
};