<template>
    <div>
        <h3>下拉框</h3>
        <v-code :code="select1" :isShow="false">
            <template v-slot:descript>
                <v-group>
                    <span class="code">data-key</span>配置下拉框的属性
                    <div>
                        <span class="code">val</span>下拉框的值
                        <br />
                        <span class="code">sortArry</span>下拉选项，value：选项值 title：选项文字
                        <br />
                        <span class="code">changeCallBack</span>切换后执行的回调，参数为下拉框的值
                        <br />
                        <span class="code">immediate</span>是否立即执行changeCallBack，默认为true
                    </div>
                </v-group>
            </template>
            <select-list></select-list>
        </v-code>
        <h3>自定义下拉框</h3>
        <v-code :code="customSelect" :isShow="false">
            <template v-slot:descript>
                <v-group>
                    <div>
                        <span class="code">hasManual</span>是否支持手动输入，默认：false
                        <br />
                        <span class="code">maxlength</span>手动输入时最大输入长度
                        <br />
                        <span class="code">manualText</span>手动配置选项的文字显示
                        <br />
                        <span class="code">valid</span>手动输入时，输入数据的验证
                    </div>
                    <div class="red">
                        注意： 当hasManual=true时，不会立即执行changeCallBack<br />
                        beforeChange： 当返回false时，不会执行changeCallBack
                    </div>
                </v-group>
            </template>
            <select-custom></select-custom>
        </v-code>
        <table class="table-default">
            <thead>
                <tr>
                    <th>
                        <span>参数</span>
                    </th>
                    <th>
                        <span>类型</span>
                    </th>
                    <th>
                        <span>默认值</span>
                    </th>
                    <th>
                        <span>意义</span>
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>
                        <span>required</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>true</span>
                    </td>
                    <td>
                        <span>是否必须输入</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>css</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>样式</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>show</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>true</span>
                    </td>
                    <td>
                        <span>是否显示</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>ignore</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>false</span>
                    </td>
                    <td>
                        <span>是否忽略验证，也可用于保存时不提交此项</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>disabled</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>false</span>
                    </td>
                    <td>
                        <span>是否禁用</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>hasManual</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>false</span>
                    </td>
                    <td>
                        <span>是否支持手动输入</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>manualText</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>
                        <span>自定义</span>
                    </td>
                    <td>
                        <span>手动输入时，下拉列表手动输入的文字</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>maxlength</span>
                    </td>
                    <td>
                        <span>Number</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>手动输入时最大输入长度</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>error</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>错误信息</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>name</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>下拉框的name，用于自动化</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>defaultVal</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>下拉框的默认值</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>immediate</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>true</span>
                    </td>
                    <td>
                        <span>是否立即执行回调函数</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>sortArray</span>
                    </td>
                    <td>
                        <span>Array</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>下拉框列表</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>val</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>下拉框值</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>valid</span>
                    </td>
                    <td>
                        <span>Array</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>自定义数据时的验证类型（详情见输入框）</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>changeCallBack</span>
                    </td>
                    <td>
                        <span>Function</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>值被修改后执行的回调，</span>
                        <strong>
                            <span>参数为下拉框的值</span>
                        </strong>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>events</span>
                    </td>
                    <td>
                        <span>Object</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>自定义事件</span>
                        <br />
                        <span>属性为事件类型，值为事件方法</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>beforeChange</span>
                    </td>
                    <td>
                        <span>Function</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>值修改之前执行的函数，返回false时不会执行changCallBack，其他则执行</span>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
import SelectList from "../example/select/select";
import SelectCustom from "../example/select/customSelect";

export default {
    components: {
        "select-list": SelectList,
        "select-custom": SelectCustom
    },
    data() {
        return {
            select1: this.$store.state.select.select1,
            customSelect: this.$store.state.select.customSelect
        };
    },
    methods: {}
};
</script>