<template>
    <div>
        <h3>复选框</h3>
        <v-code :code="checkbox1" :isShow="false">
            <template v-slot:descript>
                <v-group>
                    <span class="code">values</span>数组类型，单个复选框时生效，第一项为选中后的值（如果选项没有定义值），第二项为未选中的值；
                    <div>
                        <span class="code">sortArray</span>复选框选项，支持单个或多个
                    </div>
                    <span class="code">events</span>事件对象
                </v-group>
            </template>
            <checkbox-simple></checkbox-simple>
        </v-code>
        <h3>复选框组</h3>
        <v-code :code="checkboxGroup" :isShow="false">
            <template v-slot:descript>
                <v-group>
                    <span class="code">hasSelectAll</span>是否显示全选，默认false
                    <div>
                        <span class="code">sortArray</span>复选框选项，多个时值为数组类型，属性disabled为true时，此项禁用
                    </div>
                    <span class="code">events</span>事件对象
                </v-group>
            </template>
            <checkbox-list></checkbox-list>
        </v-code>
        <h3>dataKey属性</h3>
        <table class="table-default">
            <thead>
                <tr>
                    <th>
                        <span>参数</span>
                    </th>
                    <th>
                        <span>类型</span>
                    </th>
                    <th>
                        <span>默认值</span>
                    </th>
                    <th>
                        <span>意义</span>
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>
                        <span>required</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>false</span>
                    </td>
                    <td>
                        <span>是否必须有值</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>css</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>样式</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>show</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>true</span>
                    </td>
                    <td>
                        <span>是否显示</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>ignore</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>false</span>
                    </td>
                    <td>
                        <span>是否忽略验证，也可用于保存时不提交此项</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>disabled</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>false</span>
                    </td>
                    <td>
                        <span>是否禁用，禁用所有</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>val</span>
                    </td>
                    <td>
                        <span>String or Array</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>值</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>name</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>组件名称</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>values</span>
                    </td>
                    <td>
                        <span>Array</span>
                    </td>
                    <td>
                        <span>[true, false]</span>
                    </td>
                    <td>
                        <span>选中或不选中的值 第一项为选中的值 第二项为不选中的值</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>error</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>错误信息</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>hasSelectAll</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>false</span>
                    </td>
                    <td>
                        <span>是否有全选，多项时有效</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>sortArray</span>
                    </td>
                    <td>
                        <span>Array</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>选项列表</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>changeCallBack</span>
                    </td>
                    <td>
                        <span>Function</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>修改数据后的函数，参数为复选框的值</span>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
import { mapGetters } from "vuex";
import CheckboxSimple from "../example/checkbox/checbox";
import CheckboxList from "../example/checkbox/checkboxList";
export default {
    components: {
        "checkbox-list": CheckboxList,
        "checkbox-simple": CheckboxSimple
    },
    computed: {
        ...mapGetters("checkbox", ["checkbox1", "checkboxGroup"])
    }
};
</script>