import './css/style.scss';
import Vue from 'vue';
import App from './App.vue';
import router from './router';


import {store} from "./store/store.js";

import VueClipboard from 'vue-clipboard2';
Vue.use(VueClipboard);

import VueHighlightJS from 'vue-highlightjs';

// Tell Vue.js to use vue-highlightjs
Vue.use(VueHighlightJS);

//注册全局方法
import _global from './global';//引用
Vue.use(_global);

//Vue.config.productionTip = false

//html转码
let escapeText = function (str) {
  let newStr = str.replace(/[<" >&]/g, function ($1) {
      switch ($1) {
          case "<":
              return "&lt;";
          case ">":
              return "&gt;";
          case " ":
              return "&nbsp;";
          case "&":
              return "&amp;";
          case "\"":
              return "&quot;";
      }
  });
  return newStr;
};

Vue.prototype.toHtml = function(str) {
  let arr = str.split("\n");
  let newStr = "";
  arr.forEach(item => {
    newStr += '<div>' + escapeText(item) + '</div>';
  });
  return newStr;
}

import ReasyUIVue from '@reasy-team/reasy-ui-vue';
Vue.use(ReasyUIVue);
import '@reasy-team/reasy-ui-vue/dist/styles.css';

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
