<template>
    <div>
        <H3>数据验证</H3>
        <v-group>组件内部已集成数据验证，但未定义数据验证类型</v-group>
        <v-group>
            如需定义数据验证类型，需在
            <span class="code">Vue.prototype</span>上定义
            <span class="code">$valid</span>属性
        </v-group>
        <v-code :code="dataCheck" :isShow="true"></v-code>
        <v-group>
            验证类型可以为对象，但必须有all方法，如以下示例，也可以直接为函数
            <div>验证函数的参数为当前值加上属性args的值，后面例子会提及</div>
        </v-group>

        <h5>单组件数据验证</h5>
        <v-group>
            调用Vue的全局函数
            <span>$checkData</span>，参数为组件的对象
            <div>返回为当前数据是否正确，错误时返回false，并将组件对象的error属性修改为错误的信息。</div>
        </v-group>
        <h6>示例</h6>
        <v-code :code="check1" :isShow="false">
            <data-check></data-check>
        </v-code>
        <v-group>上述例子错误，dataKey.error会被赋值 自定义验证函数返回的值</v-group>
        <h5>多组数据验证</h5>
        <v-group>
            <div>调用Vue全局函数 <span class="code">$checkAll</span>，</div>
            <div>参数为多组单个组件的合集，当前仅支持对象方式，后续可以扩展成数组合集</div>
            <div>返回值为Boolean， true表示验证通过； false表示验证错误</div>
            <div>支持验证对象，key为数据字段，值为数据对象的配置，如：</div>
        </v-group>
        <v-code :code="groupCheck" :isShow="false">
            <example-check></example-check>
        </v-code>
        
        <!-- <v-code>
            <div v-html="code1"></div>
        </v-code> -->
    </div>
</template>
<script>
import { mapGetters } from "vuex";
import GroupCheck from "../example/dataCheck/groupCheck";
import DataCheck from "../example/dataCheck/checkData";

export default {
    components: {
        "example-check": GroupCheck,
        "data-check": DataCheck
    },
    computed: {
        ...mapGetters("dataCheck", ["dataCheck", "check1", "groupCheck"])
    },
    data() {
        return {
           
            formData: {
                sendInterval: {
                    maxlength: 5,
                    valid: {
                        type: "num",
                        args: [5, 32768]
                    }
                },
                ttlNum: {
                    maxlength: 2,
                    valid: {
                        type: "num",
                        args: [2, 10]
                    }
                },
                sendDelayTime: {
                    maxlength: 4,
                    valid: {
                        type: "num",
                        args: [1, 8192]
                    }
                },
                intDelayTime: {
                    maxlength: 2,
                    valid: {
                        type: "num",
                        args: [1, 10]
                    }
                }
            }
        };
    },
    methods: {
        submit() {
            let checkSuccess = this.$checkAll(this.formData);

            if(!checkSuccess) {
                this.$message("错误！请检查错误信息");
                return;
            }
            this.$message("验证OK，请开始你的表演");
            //your code
        }
    }
};
</script>