<template>
    <div>
        <h3>单选框</h3>
        <v-code :code="radio" :isShow="false">
            <template v-slot:descript>
                <v-group>
                    <span class="code">dataKey</span>配置属性如下：<br>
                    <span class="code">sortArray</span>单选框选项，属性为value：值，title：选项文字<br>
                    <span class="code">events</span>属性为事件类型，值为触发事件方法
                </v-group>
            </template>
            <radio-list></radio-list>
        </v-code>
        <h3>禁用单选框</h3>
        <v-code :code="disabled" :isShow="false">
            <template v-slot:descript>
                <v-group>
                    <span class="code">dataKey</span>配置属性如下：<br>
                    <span class="code">sortArray</span>单选框选项，属性为value：值，title：选项文字，disabled：是否禁用
                </v-group>
            </template>
            <radio-disabled></radio-disabled>
        </v-code>
        <h3>dataKey属性</h3>
        <table class="table-default">
            <thead>
                <tr>
                    <th>
                        <span>参数</span>
                    </th>
                    <th>
                        <span>类型</span>
                    </th>
                    <th>
                        <span>默认值</span>
                    </th>
                    <th>
                        <span>意义</span>
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>
                        <span>required</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>true</span>
                    </td>
                    <td>
                        <span>是否必须有值</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>css</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>样式</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>show</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>true</span>
                    </td>
                    <td>
                        <span>是否显示</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>ignore</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>false</span>
                    </td>
                    <td>
                        <span>是否忽略验证，也可用于保存时不提交此项</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>disabled</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>false</span>
                    </td>
                    <td>
                        <span>是否禁用，禁用所有</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>val</span>
                    </td>
                    <td>
                        <span>String or Array</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>值</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>name</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>组件名称</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>error</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>错误信息</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>sortArray</span>
                    </td>
                    <td>
                        <span>Array</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>radio选项</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>changCallBack</span>
                    </td>
                    <td>
                        <span>Function</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>修改选项后的回调事件</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>events</span>
                    </td>
                    <td>
                        <span>Object</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>key为事件类型，value为事件执行函数</span>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
import radioList from "../example/radio/radio";
import radioDisabled from "../example/radio/disabled";
export default {
    components: {
        "radio-list": radioList,
        "radio-disabled": radioDisabled
    },
    data() {
        return {
            radio: this.$store.state.radio.radio,
            disabled: this.$store.state.radio.disabled
        };
    },
    methods: {}
};
</script>