<template>
    <div>
        <v-checkbox :data-key="checkbox"></v-checkbox>
        <div>值为：{{checkbox.val}}</div>
    </div>
</template>
<script>
export default {
    data() {
        return  {
            checkbox: {
                val: [],
                hasSelectAll: true,
                sortArray: [{
                    value: 1,
                    title: "星期一"
                },
                {
                    value: 2,
                    title: "星期二",
                    disabled: true
                },
                {
                    value: 3,
                    title: "星期三"
                },
                {
                    value: 4,
                    title: "星期四"
                },
                {
                    value: 5,
                    title: "星期五"
                },
                {
                    value: 6,
                    title: "星期六"
                },
                {
                    value: 7,
                    title: "星期日"
                }],
                changeCallBack(value) {
					console.log("checkbox value ",value);
                },
                immediate: false,
                events: {
                    click: this.clickCheckbox
                }
            }
        };
    },
    methods: {
        clickCheckbox() {
            console.log("点击复选框，值为", this.checkbox.val);
        }
    }
}
</script>