<template>
    <div>
        <v-checkbox :data-key="checkbox"></v-checkbox>
        <div>值为：{{checkbox.val}}</div>
    </div>
</template>
<script>
export default {
    data() {
        return  {
            checkbox: {
                values: [true, false],
                val: false,
                sortArray: [{
                    value: true,
                    title: "开启"
                }],
                changeCallBack(value) {
					console.log("checkbox value ",value);
                },
                immediate: false,
                events: {
                    click: this.clickCheckbox
                }
            }
        };
    },
    methods: {
        clickCheckbox() {
            console.log("点击复选框，值为", this.checkbox.val);
        }
    }
}
</script>