<template>
    <div class="code-group">
        <div  v-if="!isShow">
            <div class="code-example" >
                <slot></slot>
            </div>
            <div class="code-content">
                <div class="code" :style="{'height': !show ? '0': height + 'px'}">
                    <div ref="content">
                        <div class="description">
                            <slot name="descript"></slot>
                        </div>
                        <div class="copy-group">
                            <button class="btn copy" type="button" v-clipboard:copy="code" v-clipboard:success="successCopy">复制</button>
                        </div>
                        <pre v-highlightjs="code"><code :class="type"></code></pre>
                    </div>
                </div>
            </div>
            <div @click="show = !show" class="code-footer">{{show ? "隐藏源码" : "显示源码"}}</div>
        </div>
        <div v-else class="code-content">
            <div class="code">
                <div ref="content">
                    <pre v-highlightjs="code"><code :class="type"></code></pre>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "v-code",
    props: {
        isShow: {
            type: Boolean,
            default: true
        },
        code: {
            type: String,
            default: ""
        },
        type: {
            type: String,
            default: "html hljs"
        }
    },
    data() {
        return {
           show: false,
           height: 0
        }
    },
    methods: {
        successCopy() {
            this.$message("复制成功");
        }
    },
    watch: {
        show: {
            handler(newVal) {
                if(!newVal) {
                    this.height = 0;
                } else {
                    this.$nextTick(function() {
                        this.height = this.$refs.content.offsetHeight;
                    })
                    
                }
            },
            immediate: true
        }
    }
}
</script>

<style lang="scss">
.code-group {
    display: block;
    border-radius: 4px;
    -webkit-font-smoothing: auto;
    border: 1px solid #ebebeb;
    .code-footer {
        padding: 12px 0;
       
        text-align: center;
        font-size: 14px;
        cursor: pointer;
        &:hover {
            color: rgb(77, 121, 241);
            background: #e2e2e2;
        }
    }
    .code-example {
        padding: 18px 24px;
        border-bottom: 1px solid #eee;
    }
    .description {
        background: #fff;
        margin: 20px;
        padding: 0 10px;
        border: 1px solid #eee;
        border-radius: 4px;
    }
    .copy-group {
        position: relative;
        .copy {
            position: absolute;
            right: 20px;
            border: none;
            &:hover {
                color: #d82228;
            }
        }
    }
}
.code {
    line-height: 1.8;
    font-family: Menlo,Monaco,Consolas,Courier,monospace;
    font-size: 14px;
    padding: 0 4px;
    background-color: #fafafa;
    transition: all 0.5s;
    overflow: hidden;
}
    
</style>

