<template>
    <div>
        <v-input :data-key="input"></v-input>
    </div>
</template>
<script>
export default {
    data() {
        return {
            input: {
                placeholder: "用户名",
                type: "text",
                val: "",
                maxlength: 6,
                valid: [{
                    type: "ascii",
                    args: [1, 6]
                }],
                changeCallBack: this.changeValue
            }
        };
    },
    methods: {
        changeValue(value) {
            console.log("input value is", value);
        }
    }
}
</script>