//全局函数
let valid = `
let valid = {
    num: {
        all: function(str, min, max) {
            if (!(/^([-0-9])?([0-9]+)$/).test(str)) {
                return "必须输入数字";
            }
            if (min && max) {
                if (parseInt(str, 10) < min || parseInt(str, 10) > max) {
                    return _("输入范围: %s - %s", [min, max]);
                }
            }
        }
    }
}
Vue.prototype.$valid = valid;
`;

//单组数据验证
let checkCode = `
<template>
    <div>
        <v-group title="苹果个数">
            <v-input :data-key="appleNum"></v-input>
        </v-group>
        <v-group title="">
            <v-button css="btn-primary" title="验证" :callback="checkData"></v-button>
        </v-group>
    </div>
</template>
<script>
export default {
    data() {
        return {
            appleNum: {
                maxlength: 3,
                valid: {
                    type: "num",
                    args: [1, 200]
                }
            }
        }
    },
    methods: {
        checkData() {
            let success = this.$checkData(this.appleNum);
            if(success) {
                this.$message("验证OK");
            } else {
                this.$message("错误！请检查错误信息");
            }
        }
    }
}
</script>
`;

