<template>
    <div>
        <v-switch :data-key="switch1"></v-switch>
    </div>
</template>
<script>
export default {
    data() {
        return {
           switch1: {
               val: true,
               values: [true, false],
               changeCallBack: this.changeSwitch,
               beforeChange: this.beforeChange,
               immediate: false
           }
        }
    },
    methods: {
        changeSwitch(val) {
            console.log("开关值：", val);
        },
        beforeChange() {
            this.$confirm("确定要关闭吗？").then(()=> {
                this.switch1.val = false;
            }).catch(e => {

            });
            return false;
        }
    }
}
</script>