<template>
    <div>
        <h3>基础表格</h3>
        <v-group>
            配置属性<span class="code">tableOptions</span>和<span class="code">callback</span>
            <div>
                标签<span class="code">&lt;v-table&gt;</span>
            </div>
        </v-group>
        <v-code :code="table1" :isShow="false">
            <template v-slot:descript>
                <v-group>
                    <span class="code">tableOptions</span>表格的配置信息，包含：
                    <div>
                        <span class="code">css</span>样式
                    </div>
                    <div>
                        <span class="code">key</span>关键字
                    </div>
                    <div>
                        <span class="code">columns</span>列信息
                    </div>
                    <div>
                        <span class="code">originData</span>原始数据
                    </div>
                </v-group>
                <v-group>
                    <span class="code">callback</span>表格初始化后执行的回调，参数为当前页的数据
                </v-group>
            </template>
            <table-list></table-list>
        </v-code>

        <h3>表格事件</h3>

        <v-code :code="customTable" :isShow="false">
            <template v-slot:descript>
                <v-group>
                    <div>
                        自定义事件名字必须是
                        <span class="code">@on-custom-comp</span>，函数参数需用户在自定义组件中自己定义
                        <div>
                            内置复选框组件中，支持类型
                            <span class="code">type</span>,值为checkbox和selectAll
                            <br />
                            <span class="code">index</span>当前表格中的第几行
                            <br />
                            <span class="code">rowData</span>当前表格中的第几行的数据
                            <br />
                            <span class="code">originData</span>当前表格中的第几行的原始数据
                        </div>
                        <div>
                            配置<span class="code">selectBox: true</span>时，如果希望此行复选框禁用，则配置此行数据hasCheckbox=false
                        </div>
                    </div>
                </v-group>
            </template>
            <custom-table></custom-table>
        </v-code>

        <h3>表格分页</h3>

        <v-code :code="tablePage" :isShow="false">
            <template v-slot:descript>
                <v-group>
                    <div>
                        <span class="code">showPage</span>是否分页
                        <br />
                        <span class="code">pagePer</span>每页多少条，默认10
                        <br />
                        <span class="code">maxTableRow</span>超过多少条后显示滚动条，默认10
                    </div>
                </v-group>
            </template>
            <page-table></page-table>
        </v-code>
        <h3>自定义组件</h3>
        <v-code :code="customComTable" :isShow="false">
            <template v-slot:descript>
                <v-group>
                    <div>
                        <span class="code">@on-custom-comp</span>添加自定义事件
                        <div>
                            在全局组件处理函数中的事件都交付于父组件的
                            <span class="code">on-custom-comp</span>的事件处理
                        </div>
                        <div>
                            所以在自定义事件中必须触发父元素的
                            <span class="code">on-custom-comp</span>事件
                        </div>
                    </div>
                </v-group>
            </template>
            <custom-com></custom-com>
        </v-code>
        <h3>tableData属性</h3>
        <table class="table-default">
            <thead>
                <tr>
                    <th>
                        <span>参数</span>
                    </th>
                    <th>
                        <span>类型</span>
                    </th>
                    <th>
                        <span>默认值</span>
                    </th>
                    <th>
                        <span>意义</span>
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>
                        <span>originData</span>
                    </td>
                    <td>
                        <span>Array</span>
                    </td>
                    <td>
                        <span>[]</span>
                    </td>
                    <td>
                        <span>表格原始数据</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>show</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>true</span>
                    </td>
                    <td>
                        <span>表格是否显示</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>css</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>表格自定义样式</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>key</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>表格数据的关键字，用于查找和匹配数据，每组数据的唯一标识符</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>maxTableRow</span>
                    </td>
                    <td>
                        <span>Number</span>
                    </td>
                    <td>
                        <span>10</span>
                    </td>
                    <td>
                        <span>表格显示多少行，超过行数时显示滚动条</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>showPage</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>false</span>
                    </td>
                    <td>
                        <span>是否支持分页显示</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>pagePer</span>
                    </td>
                    <td>
                        <span>Number</span>
                    </td>
                    <td>
                        <span>10</span>
                    </td>
                    <td>
                        <span>每页多少条</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>search</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>false</span>
                    </td>
                    <td>
                        <span>是否支持搜索</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>placeholder</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>搜索框的占位符，为空时会取支持搜索列的title，再以 "/" 合并</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>selectBox</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>false</span>
                    </td>
                    <td>
                        <span>第一列是否是复选框</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>secondColumns</span>
                    </td>
                    <td>
                        <span>Array</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>表格第二个title</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>columns</span>
                    </td>
                    <td>
                        <span>Array</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>表头信息</span>
                    </td>
                </tr>
            </tbody>
        </table>
        <h3>columns属性</h3>
        <table class="table-default">
            <thead>
                <tr>
                    <th>
                        <span>参数</span>
                    </th>
                    <th>
                        <span>类型</span>
                    </th>
                    <th>
                        <span>默认值</span>
                    </th>
                    <th>
                        <span>意义</span>
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>
                        <span>title</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>表头文字</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>field</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>表格字段</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>width</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>列宽度</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>search</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>此列是否支持搜索</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>sort</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>此列是否支持排序</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>format</span>
                    </td>
                    <td>
                        <span>Function</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>数据转换函数，必须有返回值，</span>
                        <br />
                        <span>第一参数为该字段的值</span>
                        <br />
                        <span>第二参数为此组数据</span>
                        <br />
                        <span>返回值为当前显示</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>parseHtml</span>
                    </td>
                    <td>
                        <span>Boolean</span>
                    </td>
                    <td>
                        <span>false</span>
                    </td>
                    <td>
                        <span>是否显示以html显示</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>componentName</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>自定义组件名称，必须为全局组件</span>
                        <br />
                        <span>其中事件处理必须触发</span>
                        <code>on-custom-comp</code>
                        <span>父组件的事件</span>
                    </td>
                </tr>
            </tbody>
        </table>
        <v-group>
            <h3>自定义组件参数</h3>
            <div>自定义组件可以从父组件拿到以下值</div>
        </v-group>
        <table class="table-default">
            <thead>
                <tr>
                    <th>
                        <span>参数</span>
                    </th>
                    <th>
                        <span>类型</span>
                    </th>
                    <th>
                        <span>默认值</span>
                    </th>
                    <th>
                        <span>意义</span>
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>
                        <span>action</span>
                    </td>
                    <td>
                        <span>Any</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>自定义的字段，用于自定义组件传值</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>rowData</span>
                    </td>
                    <td>
                        <span>Object</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>当前行的数据</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>originData</span>
                    </td>
                    <td>
                        <span>Object</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>当前行的原始数据</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>field</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>当前行列的字段</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>keyword</span>
                    </td>
                    <td>
                        <span>String</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>关键字，等于表格插件的key</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span>index</span>
                    </td>
                    <td>
                        <span>Number</span>
                    </td>
                    <td>&nbsp;</td>
                    <td>
                        <span>当前行数</span>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
import TableList from "../example/table/table";
import CustomTable from "../example/table/customTable";
import PageTable from "../example/table/tablePage";
import CustomCom from "../example/table/custom";
export default {
    components: {
        "table-list": TableList,
        "custom-table": CustomTable,
        "page-table": PageTable,
        "custom-com": CustomCom
    },
    data() {
        return {
            table1: this.$store.state.table.table1,
            customTable: this.$store.state.table.customTable,
            tablePage: this.$store.state.table.pageTable,
            customComTable: this.$store.state.table.customComTable
        };
    }
};
</script>