<template>
    <div>
        <h3>确认框</h3>
        <v-code :code="confirm" :isShow="false">
            <template v-slot:descript>
                <v-group>
                    <span class="code">this.$confirm</span>链式语法必须要有then和catch
                    <div>
                        then点击确定执行函数，catch点击取消执行函数
                    </div>
                    <div>
                        对象时支持的属性如下：
                        <span class="code">title</span>提示标题<br/>
                        <span class="code">parseHtml</span>是否以dom节点解析，默认false<br/>
                        <span class="code">okText</span>确定按钮文字<br/>
                        <span class="code">cancelText</span>取消按钮文字<br/>
                        <span class="code">content</span>提示文字或者dom节点<br/>
                    </div>
                </v-group>
            </template>
            <confirm-list></confirm-list>
        </v-code>
        <h3>警告框</h3>
        <v-code :code="alert" :isShow="false">
            <template v-slot:descript>
                <v-group>
                    参数支持对象或者是字符串
                    <div>
                        对象时支持的属性如下：
                        <span class="code">title</span>提示标题<br/>
                        <span class="code">parseHtml</span>是否以dom节点解析，默认false<br/>
                        <span class="code">okText</span>确定按钮文字<br/>
                        <span class="code">content</span>提示文字或者dom节点<br/>
                    </div>
                </v-group>
            </template>
            <alert-list></alert-list>
        </v-code>
    </div>
</template>
<script>
import ConfirmList from "../example/message/confirm";
import AlertList from "../example/message/alert";
export default {
    components: {
        "confirm-list": ConfirmList,
        "alert-list": AlertList,
    },
    data() {
        return {
            confirm: this.$store.state.message.confirm,
            alert: this.$store.state.message.alert
        };
    },
    methods: {}
};
</script>