<template>
    <div>
        <v-input :data-key="input"></v-input>
    </div>
</template>
<script>
export default {
    data() {
        return {
            input: {
                placeholder: "密码",
                type: "password",
                hasEye: true,
                val: "",
                maxlength: 6,
                valid: [{
                    type: "ascii",
                    args: [1, 6]
                }],
                events: {
                    keyup: this.keyupPwd,
                    change: this.changeValue
                }
            }
        };
    },
    methods: {
        changeValue() {
            console.log("change input value is", this.input.val);
        },
        keyupPwd() {
            console.log("keyup input value is", this.input.val);
        }
    }
}
</script>