<template>
    <div>
        <a
            v-if="hasEdit"
            class="table-action"
            v-tooltip="editText"
            @click.stop.prevent="handler('edit', field)"
            
        >{{editText}}</a>
        <a
            v-if="hasDel"
            class="table-action icon-delete"
            v-tooltip="cancelText"
            @click.stop.prevent="handler('delete', field)"
        >{{cancelText}}</a>
        <a
            v-if="hasView"
            class="table-action icon-view"
            v-tooltip="viewText"
            @click.stop.prevent="handler('view', field)"
        ></a>
        <a v-if="!hasEdit && !hasDel && !hasView">--</a>
    </div>
</template>
<script>
export default {
    name: "table-operation",
    props: ["rowData", "field", "index", "tableData", "originData", "action", "keyword"],
    data() {
        return {
            editText: "编辑",
            cancelText: "删除",
            viewText: "查看"
        };
    },
    computed: {
        hasEdit() {
            return this.action.indexOf("edit") != -1 && this.rowData.hasEdit !== false;
        },
        hasDel() {
            return this.action.indexOf("delete") != -1 && this.rowData.hasDel !== false;
        },
        hasView() {
            return this.action.indexOf("view") != -1 && this.rowData.hasView !== false;
        }
    },
    methods: {
        handler(type) {
            // 参数根据业务场景随意构造
            let params = {
                type: type,
                index: this.index,
                rowData: this.originData
            };
            this.$emit("on-custom-comp", params);
        }
    }
};
</script>
<style lang="scss">
.table-action {
    font-size: 16px;
    color: #333;
    cursor: pointer;
    & + .table-action {
        margin-left: 10px;
    }
}
</style>

